@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

*, *::before, *::after{
    box-sizing: border-box;
}

a{
    text-decoration: none;
}

body{
    padding: 0;
    margin: 0;
    background-color: #2F3038;
    font-family: Poppins, sans-serif;
    font-size: 1.2rem;
    color: white;
    min-height: 100vh;
}

.full-height-grow{
    display: flex;
    flex-direction: column;
}

.brand-logo{
    display: flex;
    align-items: center;
    font-size: 1.25rem;
    color: inherit;
    text-decoration: none;
}

.brand-logo-name{
    margin-left: 1rem;
}

.main-nav ul{
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
}

.main-nav a{
    color: inherit;
    text-decoration: none;
    padding: 1rem;
}
.main-nav a:hover{
    color: #adadad;
}
.main-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 140px;
}

.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 40px;
}

.title{
    font-weight: 500;
    font-size: 3em;
    margin-bottom: 1rem;
    margin-top: 0;
}

.subtitle{
    font-weight: 500;
    font-size:1.2em;
    margin-bottom: 2rem;
}

.btn{
    color: white;
    background-color: #1762A7;
    padding: .75rem 1.5rem;
    text-decoration: none;
    font-size: .9rem;
}